'use strict';

var ReasonReactRouter = require("reason-react/src/ReasonReactRouter.js");

function getRoutePath(route) {
  if (route) {
    return "/404";
  } else {
    return "/";
  }
}

function getRouteFromUrl(url) {
  if (url) {
    return /* NotFound */1;
  } else {
    return /* Home */0;
  }
}

function push(route) {
  return ReasonReactRouter.push(route ? "/404" : "/");
}

exports.getRoutePath = getRoutePath;
exports.getRouteFromUrl = getRouteFromUrl;
exports.push = push;
/* ReasonReactRouter Not a pure module */
